.Gameboard {
    h1 {
        color: #43a047;
    }

    .Stats {
        display: flex;
        width: 100%;
        justify-content: space-between;
        font-weight: bold;

        .Level {
            width: 50%;
            padding-left: 1rem;
        }

        .Progress {
            width: 50%;
            text-align: right;
            padding-right: 1rem;

            .WordStats {
                font-weight: normal;
            }
        }
    }

    display: flex;
    align-items: center;
    height: 100vh;
    flex-direction: column;

    .GridSection {
        height: 40vh;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;

        .Grid {
            display: flex;
            flex-direction: column;

            .Row {
                display: flex;

                .Cell {
                    text-align: center;
                    border: 3px solid #634820;
                    background-color: #966F3355;
                    border-radius: 1rem;
                    font-weight: bold;
                    cursor: pointer;
                    user-select: none;

                    &.Selected {
                        color: white
                    }
                }
            }
        }

        .Message {
            height: 28px;
            margin-top: 10px;
            opacity: 1;
            transition: opacity 0.5s;
            font-size: 20px;
            color: blue;
        }
    }

    .FoundWords {

        height: 30vh;
        padding-top: 2rem;
        width: 100%;
        text-align: center;

        h2 {
            font-size: 28;
            font-weight: normal;
            text-decoration: underline;
            color: brown;
        }

        .Words {
            display: flex;
            flex-wrap: wrap;
            align-items: flex-start;
            align-content: flex-start;

            .Word {
                display: inline-block;
                width: 50%;
                font-weight: bold;
            }
        }
    }

    .LevelComplete {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #0008;
        .Content {
            position: fixed;
            top: 50%;
            left: 50%;
            text-align: center;
            transform: translateX(-50%) translateY(-50%);
            background-color: #d4edda;
            padding: 1rem;
            color: #155724;
            border-radius: 1rem;
            width: 80%;
            max-width: 500px;

            h3 {
                color: black;
            }

            .Proceed {
                background-color: #e0a800;
                display: inline-block;
                border-radius: 3px;
                padding: 0.5rem 1rem;
                font-weight: bold;
                text-transform: uppercase;
                margin-top: 1rem;
                color: black;
                cursor: pointer;
            }
        }
    }
}